

.section{
    border-bottom: solid 1px #3a3a3a;
    margin-top: 3rem;
}

.details_title{
    font-size: 3.5rem;
    font-weight: 700;
    border-left: solid 4px #5271ff;
    padding-left: 1rem;
}

.details{
    margin-top: 2rem;
    margin-left: 10%;
}

.details_text{
    margin-top: 1rem;
    font-size: 1.1rem;
    font-weight: 400;
}

.details_navigation{
    display: flex;
    margin-top: 3rem;
  
}

.details_phone, .details_location, .details_email{
    display: flex;
    border-left: solid 2px #5271ff;
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
    padding-left: 1rem;
}

.details_location, .details_email{
    margin-left: auto   ;
}

.details_navigation_svg{
    height: 3rem;
    width: 3rem;
}

.details_navigation_data{
    margin-left: 1rem;  
}

.details_navigation_title{
    font-size: 1.5rem;
    font-weight: 600;
}

.maps{
    margin-top: 5rem;
    margin-left: 10%;
    margin-right: 30%;
    width: 90%;
    height: 16rem;
}

.maps_me{
    border: solid 0px white;
}

@media (max-width: 990px){
    .contaсt_nav_socialMedia{
        display: block;
    }

    .details{
        margin-right: 8%;
    }

    .maps{
        width: 82%;
    }
}

.contaсt_nav_socialMedia{
    display: grid;
    grid-template-columns: auto auto auto auto auto auto; 
    justify-content: center; 
    gap: 0.1rem; 
    margin-top: 1.3rem;
}


@media (max-width: 512px){
    .details{
        margin-left: 3%;
    }

    .details_title{
        font-size: 2rem;
    }

    .details_navigation{
        display: block;
    }

    .details_navigation_svg{
        width: 2rem;
    }

    .details_navigation_title{
        font-size: 1.2rem;
    }

    .details_navigation_text{
        font-size: 0.6rem;
    }

    .details_phone, .details_location, .details_email{
        padding-top: 0.6rem;
        padding-bottom: 0.6rem;
        margin-bottom: 0.7rem;
    }

    .maps{
        margin-top: 2rem;
    }
}
